<template>
  <div class="login">
    <div class="form-wrapper">
      <el-input placeholder="请输入用户名" class="userName" v-model="userName" clearable></el-input>
      <el-input placeholder="请输入密码" class="pwd" v-model="password" show-password></el-input>
      <el-button type="primary" @click.enter="check_form" @keydown.enter="check_form">登录</el-button>
      <p class="to_register">
        <span>还没有账号?</span>
        <router-link class="link" :to="{path:'/register'}">免费注册</router-link>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      userName: "",
      password: ""
    };
  },
  methods: {
    // 表单验证
    check_form() {
      if (!this.userName || !this.password) {
        const tips = !this.userName ? "用户名" : "密码";
        this.$message({
          showClose: true,
          message: `请输入${tips}!`,
          type: "error"
        });
      } else {
        this.submit_login();
      }
    },
    //提交登录请求
    submit_login() {
      this.$api
        .login("/login", {
          userName: this.userName,
          password: this.password
        })
        .then(res => {
          console.log(res);
          const userinfo = res.data.data;
          if (this.checke_response(res)) {
            this.store_info(userinfo);
            this.login_success(res);
          } else {
            this.login_failed(res);
          }
        });
    },
    //验证返回结果
    checke_response(res) {
      return res.data.status === "ok" ? true : false;
    },
    //登录成功
    login_success(res) {
      this.$router.push({ path: "/home" });
    },
    //登录失败
    login_failed(res) {
      this.$message({
        showClose: true,
        message: res.data.msg,
        type: "error"
      });
    },
    //用户信息存入vuex
    store_info(info) {
      this.$store.commit({
        type: "set_user_info",
        info
      });
    }
  }
};
</script>

<style scoped lang="scss">
.login {
  text-align: center;
  .form-wrapper {
    width: 400px;
    position: absolute;
    left: 50%;
    top: 120px;
    transform: translate(-50%);
    .pwd {
      margin: 40px 0;
    }
    .to_register {
      .link {
        text-decoration: none;
        color: #fe8c00;
      }
    }
  }
}
</style>


